<template>
    <span
        tabindex="0"
        :class="wrapClasses"
        @click="toggle"
        @keydown.space="toggle"
    >
        <input type="hidden" :name="name" :value="currentValue">
        <span :class="innerClasses">
            <slot name="open" v-if="currentValue === trueValue"></slot>
            <slot name="close" v-if="currentValue === falseValue"></slot>
        </span>
    </span>
</template>
<script>
    import { oneOf } from '../../utils/assist';
    import Emitter from '../../mixins/emitter';

    const prefixCls = 'ivu-switch';

    export default {
        name: 'iSwitch',
        mixins: [ Emitter ],
        props: {
            value: {
                type: [String, Number, Boolean],
                default: false
            },
            trueValue: {
                type: [String, Number, Boolean],
                default: true
            },
            falseValue: {
                type: [String, Number, Boolean],
                default: false
            },
            disabled: {
                type: Boolean,
                default: false
            },
            size: {
                validator (value) {
                    return oneOf(value, ['large', 'small', 'default']);
                },
                default () {
                    return !this.$IVIEW || this.$IVIEW.size === '' ? 'default' : this.$IVIEW.size;
                }
            },
            name: {
                type: String
            },
            func: {
                type: Function,
            },
            // loading: {
            //     type: Boolean,
            //     default: false
            // }
        },
        data () {
            return {
                currentValue: this.value,
                loading: false,
            };
        },
        computed: {
            wrapClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-checked`]: this.currentValue === this.trueValue,
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-${this.size}`]: !!this.size,
                        [`${prefixCls}-loading`]: this.loading,
                    }
                ];
            },
            innerClasses () {
                return `${prefixCls}-inner`;
            }
        },
        methods: {
            toggle (event) {
                event.preventDefault();
                if (this.disabled || this.loading) {
                    return false;
                }
                // func 为父级下发的一个 Promise 函数，执行之后会把当前选中状态及 Promise 返回值发送给父级
                if (typeof this.func !== 'function') {
                    const checked = this.currentValue === this.trueValue ? this.falseValue : this.trueValue;
                    this.currentValue = checked;
                    this.$emit('input', checked, event);
                    this.$emit('on-change', checked, event);
                    this.dispatch('FormItem', 'on-form-change', checked, event);
                } else {
                    this.loading = true;
                    this.func().then((res) => {
                        this.loading = false;
                        if (res) {
                            this.currentValue = !this.currentValue;
                            this.$emit('input', this.currentValue, event);
                            this.$emit('on-done', this.currentValue, res);
                        } else {
                            this.$emit('on-fail', res);
                        }
                    }).catch((err) => {
                        this.loading = false;
                        this.$emit('on-fail', err);
                    });
                }
            }
        },
        watch: {
            value (val) {
                if (val !== this.trueValue && val !== this.falseValue) {
                    throw 'Value should be trueValue or falseValue.';
                }
                this.currentValue = val;
            }
        }
    };
</script>



